"use server"

interface SubmitWaitlistFormResult {
  success: boolean
  error?: string
}

export async function submitWaitlistForm(
  name: string,
  email: string,
  phone: string
): Promise<SubmitWaitlistFormResult> {
  try {
    const notionApiKey = process.env.NOTION_API_KEY
    const notionDatabaseId = process.env.NOTION_DATABASE_ID

    // ÌôòÍ≤ΩÎ≥ÄÏàò Í≤ÄÏ¶ù
    if (!notionApiKey) {
      console.error("NOTION_API_KEYÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.")
      return {
        success: false,
        error: "ÏÑúÎ≤Ñ ÏÑ§Ï†ï Ïò§Î•ò: API ÌÇ§Í∞Ä ÏóÜÏäµÎãàÎã§.",
      }
    }

    if (!notionDatabaseId) {
      console.error("NOTION_DATABASE_IDÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.")
      return {
        success: false,
        error: "ÏÑúÎ≤Ñ ÏÑ§Ï†ï Ïò§Î•ò: Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ IDÍ∞Ä ÏóÜÏäµÎãàÎã§.",
      }
    }

    // Notion API Ìò∏Ï∂ú
    const response = await fetch("https://api.notion.com/v1/pages", {
      method: "POST",
      headers: {
        Authorization: `Bearer ${notionApiKey}`,
        "Content-Type": "application/json",
        "Notion-Version": "2022-06-28",
      },
      body: JSON.stringify({
        parent: {
          database_id: notionDatabaseId,
        },
        properties: {
          Ïù¥Î¶Ñ: {
            title: [
              {
                text: {
                  content: name,
                },
              },
            ],
          },
          Ïù¥Î©îÏùº: {
            email: email,
          },
          Ï†ÑÌôîÎ≤àÌò∏: {
            phone_number: phone,
          },
        },
      }),
    })

    // ÏùëÎãµ Ï≤òÎ¶¨
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}))
      console.error("Notion API Ïò§Î•ò ÏùëÎãµ:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      })

      // ÏóêÎü¨ Î©îÏãúÏßÄ Ï∂îÏ∂ú
      let errorMessage = `API Ïò§Î•ò (${response.status})`
      if (errorData.message) {
        errorMessage = errorData.message
      } else if (errorData.code) {
        errorMessage = `Ïò§Î•ò ÏΩîÎìú: ${errorData.code}`
      }

      return {
        success: false,
        error: errorMessage,
      }
    }

    // ÏÑ±Í≥µ ÏùëÎãµ ÌôïÏù∏
    const result = await response.json().catch(() => null)
    if (result) {
      console.log("Notion Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§Ïóê ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï†ÄÏû•ÎêòÏóàÏäµÎãàÎã§:", result.id)
    }

    return {
      success: true,
    }
  } catch (error) {
    console.error("Ìèº Ï†úÏ∂ú Ï§ë ÏòàÏô∏ Î∞úÏÉù:", error)
    return {
      success: false,
      error:
        error instanceof Error
          ? error.message
          : "ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.",
    }
  }
}

